import Vue from 'vue'
import Router from 'vue-router'

import PageDashboard from '../../pages/dashboard/route'
import PageSubAgents from '../../pages/sub-agents/route'
import PageManagePlayers from '../../pages/manage-players/route'
import PageBet from '../../pages/bet/route'
import PagePlayerBetHistory from '../../pages/player-bet-history/route'
import PageLoadPlayerPoints from '../../pages/load-player-points/route'
import PagePlayerPointsLedger from '../../pages/player-points-ledger/route'
import PageWinners from '../../pages/winners/route'
import PageDraws from '../../pages/draws/route'
import PageCommission from '../../pages/commission/route'

Vue.use(Router)

let routes = [
  {
    path: '/',
    redirect: '/dashboard'
  },
  PageDashboard,
  PageSubAgents,
  PageManagePlayers,
  PageBet,
  PagePlayerBetHistory,
  PageLoadPlayerPoints,
  PagePlayerPointsLedger,
  PageWinners,
  PageDraws,
  PageCommission
]

let router = new Router({ routes })
router.beforeEach((to, from, next) => {
  if (to.name) {
    next()
  } else {
    next(new Error('Page not accessible'))
  }
})

export default {
  routes,
  router
}


