import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

import Config from '../config'
import Utils from '../utils'

const API_BASE = 'https://n8games.cc/api'
const API_LOGIN = `${ API_BASE }/v1/login`
const API_SUB_AGENT = `${ API_BASE }/v1/subagent`
const API_PLAYER = `${ API_BASE }/v1/player`
const API_DRAW_LIST = `${ API_BASE }/v1/drawlists`

export const store = new Vuex.Store({
  state: {
    token: null,
    user: null
  },

  getters: {
    token(state) {
      return state.token
    },

    user(state) {
      return state.user
    }
  },

  mutations: {
    SET_TOKEN(state, token) {
      state.token = token
    },

    SET_USER(state, user) {
      state.user = user
    }
  },

  actions: {
    async login({ commit }, user) {

      let { data }  = await axios({
        method: 'POST',
        url: API_LOGIN,
        data: user
      })

      let item = await Utils.Auth.encrypt(data)

      localStorage.setItem(Config.AUTH_KEY, item)
      location.reload()
    },

    async logout({ commit }) {
      commit('SET_TOKEN', null)
      commit('SET_USER', null)

      localStorage.removeItem(Config.AUTH_KEY)
      location.href = ''
    },

    // Sub-agent
    async subAgentList({ state }) {
      let { data } = await axios({
        headers: {
          Authorization: `Bearer ${ state.token }`
        },
        url: API_SUB_AGENT
      })

      return data
    },

    async getSubAgent({ state }, id) {
      let { data } = await axios({
        headers: {
          Authorization: `Bearer ${ state.token }`
        },
        url: `${ API_SUB_AGENT }/${ id }`
      })

      return data[0]
    },

    async removeSubAgent({ state }, id) {
      await axios({
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${ state.token }`
        },
        url: `${ API_SUB_AGENT }/${ id }`
      })
    },

    async saveSubAgent({ state }, data) {
      await axios({
        method: data.id ? 'PUT' : 'POST',
        headers: {
          Authorization: `Bearer ${ state.token }`
        },
        url: `${ API_SUB_AGENT }${ data.id ? `/${ data.id }` : '' }`,
        data
      })
    },

    // Player
    async playerList({ state }) {
      let { data } = await axios({
        headers: {
          Authorization: `Bearer ${ state.token }`
        },
        url: API_PLAYER
      })

      return data
    },

    async getPlayer({ state }, id) {
      let { data } = await axios({
        headers: {
          Authorization: `Bearer ${ state.token }`
        },
        url: `${ API_PLAYER }/${ id }`
      })

      return data
    },

    async removePlayer({ state }, id) {
      await axios({
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${ state.token }`
        },
        url: `${ API_PLAYER }/${ id }`
      })
    },

    async savePlayer({ state }, data) {
      await axios({
        method: data.id ? 'PUT' : 'POST',
        headers: {
          Authorization: `Bearer ${ state.token }`
        },
        url: `${ API_PLAYER }${ data.id ? `/${ data.id }` : '' }`,
        data
      })
    },

    // Draws
    async drawList({ state }) {
      let { data } = await axios({
        headers: {
          Authorization: `Bearer ${ state.token }`
        },
        url: API_DRAW_LIST
      })

      return data
    }
  }
})

Vue.prototype.$store = store


