import Vue from 'vue'
import moment from 'moment'

Vue.directive('time-format', (el, binding) => {
  let value = binding.value

  if (   value
      && value.time) {
    if (value.format) {
      el.innerHTML = moment(value.time, 'HH:mm').format(value.format)
    } else {
      el.innerHTML = moment(value.time, 'HH:mm')
    }
  }
})



